.App {
  margin: 0;
  padding: 20px 0 0 10px;
  background-color: #f0f0f0;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100vh;   
}

.phone {
  background-color: #fff;
  border: 10px solid #ccc;
  border-radius: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  width: 600px; /* Set the width of the phone display */
  height: 900px; /* Set the height of the phone display */
  overflow: hidden;
  position: relative;
}

.screen {
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 0px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
